<template lang="html">
  <div class="">
    <div class="tal">
      <el-form :inline="true" :model="searchFrom" class="demo-form-inline">
        <el-form-item label="">
          <el-input v-model="searchFrom.userName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="searchFrom.loginName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="small" @click="onSubmit"></el-button>
          <el-button type="warning" size="small" @click="cleanForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="line"></div>
    <el-row class="tal p10">
      <el-button type="primary" size="small" icon="el-icon-plus" @click="newDialogVisible = true"></el-button>
  </el-row>
  <el-row>
    <el-table
    :data="tableData"
    height="400"
    border
    style="width: 100%">
    <el-table-column
      prop="userName"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="loginName"
      label="">
    </el-table-column>
    <el-table-column
      prop="createDate"
      label="">
    </el-table-column>
    <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="changePwd(scope.$index, scope.row)"></el-button>
            <el-button
              size="mini"
              type="danger"
              v-if="!(scope.row.loginName == 'admin')"
              @click="deleteUser(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
  </el-table>
  </el-row>
  <el-row class="tal p10">
  <el-pagination
    @size-change="handleSizeChange"
    @current-change="handleCurrentChange"
    :current-page="pageNum"
    :page-sizes="[10, 20, 50, 100]"
    :page-size="pageSize"
    layout="total, sizes, prev, pager, next, jumper"
    :total="total">
  </el-pagination>
  </el-row>

  <!--  -->
    <el-dialog
    title=""
    :visible.sync="newDialogVisible"
    width="30%"
    center>
      <el-form :label-position="labelPosition" :rules="addUserRules" :model="newData" ref="newData" label-width="80px">
        <el-form-item label="" prop="userName">
          <el-input v-model="newData.userName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="loginName">
          <el-input v-model="newData.loginName"></el-input>
        </el-form-item>
        <el-form-item label=""  prop="pwd">
          <el-input type="password" v-model="newData.pwd"></el-input>
        </el-form-item>
      </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="saveAddUser('newData')"> </el-button>
      <el-button @click="newDialogVisible = false"> </el-button>
    </span>
    </el-dialog>

    <!--  -->
      <el-dialog
      title=""
      :visible.sync="pwdDialogVisible"
      width="30%"
      center>
        <el-form :label-position="labelPosition" :rules="uptPwdRules" :model="uptPwd" ref="uptPwd" label-width="100px">
          <el-form-item label="" prop="oldPwd">
            <el-input type="password" v-model="uptPwd.oldPwd"></el-input>
          </el-form-item>
          <el-form-item label="" prop="newPwd">
            <el-input type="password" v-model="uptPwd.newPwd"></el-input>
          </el-form-item>
          <el-form-item label=""  prop="newPwdRe">
            <el-input type="password" v-model="uptPwd.newPwdRe"></el-input>
          </el-form-item>
        </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveUptPwd('uptPwd')"> </el-button>
        <el-button @click="pwdDialogVisible = false"> </el-button>
      </span>
      </el-dialog>
  </div>
</template>

<script>
// 
import {
  userPage,
  userDel,
  userAdd,
  updPwd
} from '../../axios/api'
import common from '../../common/index'
export default {
  name: 'userManage',
  data() {
    var validatePass = (rule, value, callback) => {
      if (value !== this.uptPwd.newPwd) {
        callback(new Error(''));
      } else {
        callback();
      }
    }
    var validateUserName = (rule, value, callback) => {
      var regEn = /[`~!@#$%^&*()_+<>?:"{},.\/;'[\]]/im,regCn = /[#|[\]]/im
      if(regEn.test(value) || regCn.test(value)) {
        callback(new Error(''))
      }else{
        callback()
      }
    }
    var validateLoginName = (rule, value, callback) => {
      var g = /^[a-zA-Z0-9]{1,20}$/
      if(g.test(value)){
        callback()
      }else{
        callback(new Error(''))
      }
    }
    return {
      searchFrom:{
        userName:'',
        loginName:''
      },
      newDialogVisible: false,
      pwdDialogVisible: false,
      labelPosition: 'left',
      newData: {
        userName: '',
        loginName: '',
        pwd: ''
      },
      uptPwd: {
        oldPwd: '',
        newPwd: '',
        newPwdRe: '',
        userId: ''
      },
      tableData: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      addUserRules: {
        userName: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            max: 20,
            message: '20',
            trigger: 'blur'
          },
          {
            validator: validateUserName,
            trigger: 'blur'
          }],
        loginName: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            max: 20,
            message: '20',
            trigger: 'blur'
          },
          {
            validator: validateLoginName,
            trigger: 'blur'
          }],
        pwd: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 6,
            message: '6',
            trigger: 'blur'
          },
          {
            max: 20,
            message: '20',
            trigger: 'blur'
          }]
      },
      uptPwdRules: {
        oldPwd: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 6,
            message: '6',
            trigger: 'blur'
          },
          {
            max: 20,
            message: '20',
            trigger: 'blur'
          }],
        newPwd: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 6,
            message: '6',
            trigger: 'blur'
          },
          {
            max: 20,
            message: '20',
            trigger: 'blur'
          }],
        newPwdRe: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            validator: validatePass,
            trigger: 'blur'
          }]
      }
    }
  },
  mounted() {
    this.loadData()
  },
  watch: {
    pwdDialogVisible(newValue, oldValue) {
      var that = this
      if(!newValue) {
        that.uptPwd.oldPwd = ''
        that.uptPwd.newPwd = ''
        that.uptPwd.newPwdRe = ''
        that.uptPwd.userId = ''
        that.$refs['uptPwd'].resetFields()
      }
    },
    newDialogVisible(newValue, oldValue) {
      var that = this
      if(!newValue) {
        that.newData.userName = ''
        that.newData.loginName = ''
        that.newData.pwd = ''
        that.$refs['newData'].resetFields()
      }
    }
  },
  methods: {
    /**
     * 
     * @return {[type]} [description]
     */
    changePwd(index, row) {
      console.log(index)
      console.log(row)
      var that = this
      that.uptPwd.userId = row.userId
      that.pwdDialogVisible = true
    },
    //
    saveUptPwd(formName) {
      var that = this
      var token = common.storage.get('token')
      that.$refs[formName].validate( (valid) => {
        if(valid) {
          var sendData = {
            userId: that.uptPwd.userId,
            oldPwd: that.uptPwd.oldPwd,
            newPwd: that.uptPwd.newPwd
          }
          updPwd(sendData, token).then(res => {
            console.log(res)
            var resJson = res.data
            if(resJson && resJson.data && resJson.data.code === 0) {
              //
              that.$message({
                type: 'success',
                message: ''
              })
              that.$refs[formName].resetFields()
              that.pwdDialogVisible = false
              that.loadData()
            }else {
              //
              that.$message({
                type: 'info',
                message: resJson.data.msg
              })
            }
          }).catch(err => {
            that.$message({
              type: 'error',
              message: ''
            })
          })
        }else {
          return false
        }
      })
    },
    //
    saveAddUser(formName) {
      var that = this
      let token = common.storage.get('token')
      that.$refs[formName].validate((valid) => {
        if (valid) {
          userAdd(that.newData, token).then(res => {
            var resJson = res.data
            if (resJson && resJson.data && resJson.data.code === 0) {
              //
              that.$message({
                type: 'success',
                message: ''
              })
              that.$refs[formName].resetFields()
              that.newDialogVisible = false
              that.loadData()
            } else {
              //
              that.$message({
                type: 'info',
                message: resJson.data.msg
              })
            }
          }).catch(err => {
            that.$message({
              type: 'error',
              message: ''
            })
          })
        } else {
          return false
        }
      })
    },
    //
    deleteUser(index, row) {
      var that = this
      var token = common.storage.get('token')
      that.$alert('', '', {
        confirmButtonText: '',
        callback: action => {
          //
          if (action === 'confirm') {
            //
            let sendData = {
              userId: row.userId
            }
            userDel(sendData, token).then(res => {
              var resJson = res.data
              if (resJson && resJson.code === 0) {
                //
                that.$message({
                  type: 'success',
                  message: ''
                })
                that.loadData()
              } else {
                that.$message({
                  type: 'warning',
                  message: ''
                })
              }
            }).catch(err => {
              console.log(err)
              that.$message({
                type: 'error',
                message: ''
              })
            })
          } else {
            //
            this.$message({
              type: 'info',
              message: ``
            })
          }

        }
      })
    },
    /**
     * 
     * @return {[type]} []
     */
    onSubmit() {
      console.log(this.searchFrom)
      this.loadData()
    },
    /**
     * 
     * @return {[type]} []
     */
    cleanForm() {
      this.searchFrom.userName = '';
      this.searchFrom.loginName = '';
    },
    /**
     * 
     * @return {[type]} []
     */
    loadData() {
      var that = this
      var token = common.storage.get('token')
      var loginName = common.storage.get('userName')
      let data = {
        userName: that.searchFrom.userName,
        loginName: that.searchFrom.loginName,
        pageNum: that.pageNum,
        pageSize: that.pageSize
      }
      userPage(data, token).then(res => {
        console.log(res.data)
        if (res.data && res.data.code === 0) {
          //
          that.pageNum = res.data.data.pageNum
          that.pageSize = res.data.data.pageSize
          that.total = res.data.data.total
          that.tableData = res.data.data.list

        } else {
          //
          that.$message.warning('')
        }
      }).catch(err => {
        console.log(err)
      })
    },
    /**
     * pageSize
     * @param  {[type]} val [pageSize]
     * @return {[type]}     []
     */
    handleSizeChange(val) {
      this.pageSize = val
      this.pageNum = 1
      this.loadData()
    },
    /**
     * 
     * @param  {[type]} val [key]
     * @return {[type]}     []
     */
    handleCurrentChange(val) {
      this.pageNum = val
      this.loadData()
    }
  }
}
</script>

<style lang="css">
</style>



