<template lang="html">
  <div class="">
    <div class="tal">
      <el-form :inline="true" :model="searchFrom" class="demo-form-inline">
        <el-form-item label="">
          <el-date-picker
            v-model="searchFrom.forceTime"
            type="daterange"
            :clearable="clearable" 
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            :default-time="['00:00:00', '23:59:59']">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="searchFrom.status"></el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="searchFrom.price" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="searchFrom.code" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="small" @click="onSubmit"></el-button>
          <el-button type="warning" size="small" @click="cleanForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="line"></div>
    <el-row class="tal p10">
      <el-button type="primary" size="small" icon="el-icon-plus" @click="openPriceAdd"></el-button>
      <el-button type="primary" size="small" icon="el-icon-edit-outline" @click="priceChangeCode"></el-button>
      <el-upload
        class="upload-file ml10"
        :action="upload.action"
        :multiple="upload.multiple"
        :accept="upload.accept"
        :headers="upload.headers"
        :file-list="upload.file"
        :before-upload="beforeUpload"
        :on-success="uploadSuccess"
        :on-error="uploadError"
        :on-progress="uploadProgress"
        :on-change="uploadChange"
        >
        <el-button size="small" type="primary" icon="el-icon-caret-top"></el-button>
      </el-upload>
      <el-button class="ml10" type="primary" size="small" icon="el-icon-caret-bottom" @click="priceExport"></el-button>
      <el-button type="primary" size="small" icon="el-icon-download" @click="priceTemplate"></el-button>
      <el-button type="primary" size="small" icon="el-icon-printer" @click="print"></el-button>
  </el-row>
  <el-row>
    <el-table
    :data="tableData"
    height="400"
    border
    style="width: 100%">
    <el-table-column
      type="index"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="versionCode"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="beginDate"
      label="">
    </el-table-column>
    <el-table-column
      prop="endDate"
      label="">
    </el-table-column>
    <el-table-column
      prop="price"
      label="">
    </el-table-column>
    <el-table-column
      prop="priceCode"
      label="">
    </el-table-column>
  </el-table>
  </el-row>
  <el-row class="tal p10">
  <el-pagination
    @size-change="handleSizeChange"
    @current-change="handleCurrentChange"
    :current-page="pageNum"
    :page-sizes="[10, 20, 50, 100]"
    :page-size="pageSize"
    layout="total, sizes, prev, pager, next, jumper"
    :total="total">
  </el-pagination>
  </el-row>

  <div id="print" style="display:none;">
    <el-row style="width:100%">
      <table cellspacing="0" cellpadding="0" 
      style="width: 100%;text-align: center;border-collapse: collapse;border: 1px solid #ebeef5;">
        <tr style="font-weight: bold;height: 40px;line-height: 40px;">
          <td width="10%" 
          style="text-align: center;border-bottom: 1px solid #ebeef5;border-right: 1px solid #ebeef5;"></td>
          <td width="10%" 
          style="text-align: center;border-bottom: 1px solid #ebeef5;border-right: 1px solid #ebeef5;"></td>
          <td width="25%" 
          style="text-align: center;border-bottom: 1px solid #ebeef5;border-right: 1px solid #ebeef5;"></td>
          <td width="25%" 
          style="text-align: center;border-bottom: 1px solid #ebeef5;border-right: 1px solid #ebeef5;"></td>
          <td width="20%" 
          style="text-align: center;border-bottom: 1px solid #ebeef5;border-right: 1px solid #ebeef5;"></td>
          <td width="10%" 
          style="text-align: center;border-bottom: 1px solid #ebeef5;border-right: 1px solid #ebeef5;"></td>
        </tr>
        <tr v-for="(item,index) in printData" style="height: 40px;line-height: 40px;">
          <td style="text-align: center;border-bottom: 1px solid #ebeef5;border-right: 1px solid #ebeef5;">{{index + 1}}</td>
          <td style="text-align: center;border-bottom: 1px solid #ebeef5;border-right: 1px solid #ebeef5;">{{item.versionCode}}</td>
          <td style="text-align: center;border-bottom: 1px solid #ebeef5;border-right: 1px solid #ebeef5;">{{item.beginDate}}</td>
          <td style="text-align: center;border-bottom: 1px solid #ebeef5;border-right: 1px solid #ebeef5;">{{item.endDate}}</td>
          <td style="text-align: center;border-bottom: 1px solid #ebeef5;border-right: 1px solid #ebeef5;">{{item.price}}</td>
          <td style="text-align: center;border-bottom: 1px solid #ebeef5;border-right: 1px solid #ebeef5;">{{item.priceCode}}</td>
        </tr>
      </table>
    </el-row>
  </div>


<!--  -->
  <el-dialog
    title=""
    :visible.sync="newDialogVisible"
    width="30%"
    center>
      <el-form :label-position="labelPosition" :rules="newDataRules" :model="newData" ref="newData" label-width="80px">
        <el-form-item label="" prop="version">
          <el-input v-model="newData.version" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="price">
          <el-input v-model="newData.price" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=""  prop="code">
          <el-input v-model="newData.code" disabled></el-input>
        </el-form-item>
      </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="priceAdd('newData')"> </el-button>
      <el-button @click="newDialogVisible = false"> </el-button>
    </span>
    </el-dialog>
  </div>
</template>

<script>
// 
import {
  pricePage,
  pricePrint,
  getPriceVersionAndCode,
  priceImport,
  priceExport,
  priceAdd,
  priceChangeCode,
  priceTemplate,
  priceValidate,
  downloadErrorExcel
} from '../../axios/api'
import common from '../../common/index'
import api from '../../axios/url'
export default {
  name: 'priceManage',
  data() {
    var validatePass = (rule, value, callback) => {
      var that = this
      let json = {price:value}
      //token
      let token = common.storage.get('token')
      //
      priceValidate(json, token).then(res => {
        let resJson = res.data
        if (resJson && resJson.code === 0) {
          if (resJson.data.code === 0) {
            callback()
          }else{
            callback(new Error(''))
          }
        } else {
          callback(new Error(''));
        }
      }).catch(err => {
        console.log(err)
      })
    }
    var validateDecimal = (rule, value, callback) => {
      var that = this
      var g = /^\d+(?:\.\d{1,5})?$/
      if(g.test(value)){
        callback()
      }else{
        callback(new Error(''))
      }
    }
    return {
      clearable: false,
      newDialogVisible: false,
      labelPosition: 'left',
      newData: {
        version: 'v1.0',
        price: null,
        code: null
      },
      newDataRules: {
        price: [{
          required: true,
          message: '',
          trigger: 'blur'
        },{
          validator: validateDecimal,
          trigger: 'blur'
        },{
          validator: validatePass,
          trigger: 'blur'
        }]
      },
      searchFrom: {
        forceTime: [],
        status: false,
        price: '',
        code: '',
      },
      pageNum: 1,
      pageSize: 10,
      total: 0,
      tableData: [],
      printData: [],
      upload: {
        action: api.priceImport,
        multiple: false,
        headers: {
          token: common.storage.get('token')
        },
        accept: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel',
        showFileList: false,
        file: []
      },
      loading: null
    }
  },
  mounted() {
    this.loadPricePage()
  },
  watch: {
    newDialogVisible(newValue, oldValue) {
      var that = this
      if(!newValue) {
        that.$refs['newData'].resetFields()
      }
    }
  },
  methods: {
    //
    priceTemplate() {
      var that = this
      let json = {}
      //token
      let token = common.storage.get('token')
      priceTemplate(json, token).then(res => {
        let fileName = '.xls'
        common.downloadBlobFile(fileName, res.data)
      }).catch(err => {
        console.log(err)
      })
    },
    //
    beforeUpload(file) {
      this.loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
    },
    //
    uploadProgress(event, file, fileList) {
    },
    //
    uploadSuccess(res, file, fileList) {
      var that = this
      that.loading.close()
      console.log(res)
      if(res && res.code === 0) {
        if (res.data.code === 0) {
          //
          that.$message({message: '', type: 'success'})
          that.loadPricePage();
        }else if (res.data.code === 1) {
          //
          that.$alert('', res.data.msg, {
            confirmButtonText: '',
            callback: action => {
              if (action === 'confirm') {
                //
                let json = {
                  newFileName:res.data.newFileName
                }
                //token
                let token = common.storage.get('token')
                downloadErrorExcel(json, token).then(res => {
                  common.downloadBlobFile('.xls', res.data)
                }).catch(err => {
                  console.log(err)
                })
              }
            }
          })
        }else if (res.data.code === 2) {
          that.$message.warning('')
        }
      }else {
        that.$message.warning('')
      }
    },
    //
    uploadError(err, file, fileList) {
      var that = this
      that.loading.close()
      that.$message.error('')
    },
    //change
    uploadChange(file, fileList) {
    },
    //
    priceExport() {
      var that = this
      let json = {
        beginDateOne: that.searchFrom.forceTime[0] === undefined ? '' : common.dateFmt("yyyy-MM-dd hh:mm:ss", that.searchFrom.forceTime[0]),
        beginDateTwo: that.searchFrom.forceTime[1] === undefined ? '' : common.dateFmt("yyyy-MM-dd hh:mm:ss", that.searchFrom.forceTime[1]),
        price: that.searchFrom.price,
        code: that.searchFrom.code,
        status: that.searchFrom.status ? 0 : ''
      }
      //token
      let token = common.storage.get('token')
      priceExport(json, token).then(res => {
        let fileName = '.xls'
        common.downloadBlobFile(fileName, res.data)
      }).catch(err => {
        console.log(err)
      })
    },
    //
    priceChangeCode() {
      var that = this
      let json = {}
      that.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).then(() => {
        //token
        let token = common.storage.get('token')
        //
        priceChangeCode(json, token).then(res => {
          let resJson = res.data
          if (resJson && resJson.code === 0) {
            that.$message('')
            that.loadPricePage()
          } else {
            //
            that.$message.warning('')
          }
        }).catch(err => {
          console.log(err)
        })
      }).catch(() => {
        that.$message({
          type: 'info',
          message: ''
        })
      })
    },
    //
    priceAdd(formName) {
      var that = this
      that.$refs[formName].validate((valid) => {
        if(valid){
          let json = {
            price: that.newData.price
          }
          //token
          let token = common.storage.get('token')
          //
          priceAdd(json, token).then(res => {
            let resJson = res.data
            if (resJson && resJson.code === 0) {
              that.$message('')
              that.newDialogVisible = false
              that.loadPricePage()
            } else {
              that.$message.warning('')
            }
          }).catch(err => {
            console.log(err)
          })
        }
      })
    },
    //
    openPriceAdd() {
      var that = this
      let json = {}
      //token
      let token = common.storage.get('token')
      //
      getPriceVersionAndCode(json, token).then(res => {
        let resJson = res.data
        if (resJson && resJson.code === 0) {
          //
          that.newData.version = resJson.data.versionCode
          that.newData.code = resJson.data.priceCode
          //
          that.newDialogVisible = true
        } else {
          //
          that.$message.warning('')
        }
      }).catch(err => {
        console.log(err)
      })
    },
    /**
     * 
     * @return {[type]} []
     */
    onSubmit() {
      console.log(this.searchFrom)
      this.loadPricePage()
    },
    /**
     * 
     * @return {[type]} []
     */
    cleanForm() {
      this.searchFrom.forceTime = [];
      this.searchFrom.price = '';
      this.searchFrom.status = false;
      this.searchFrom.code = '';
    },
    /**
     * 
     * @return {[type]} []
     */
    print() {
      let newWindow = window.open()
      console.log(newWindow)
      //token
      var that = this
      let token = common.storage.get('token')
      let json = {
        beginDateOne: that.searchFrom.forceTime[0] === undefined ? '' : common.dateFmt("yyyy-MM-dd hh:mm:ss", that.searchFrom.forceTime[0]),
        beginDateTwo: that.searchFrom.forceTime[1] === undefined ? '' : common.dateFmt("yyyy-MM-dd hh:mm:ss", that.searchFrom.forceTime[1]),
        price: that.searchFrom.price,
        code: that.searchFrom.code,
        status: that.searchFrom.status ? 0 : '',
      }
      pricePrint(json, token).then(res => {
        let resJson = res.data
        if (resJson && resJson.code === 0) {
          //
          that.printData = resJson.data
          setTimeout(function() {
            that.doPrint(newWindow)
          }, 300)

        } else {
          //
          that.$message.warning('')
        }
      }).catch(res => {

      })
    },
    //
    doPrint(newWindow) {
      let printContent = document.getElementById("print").innerHTML;   //
      newWindow.document.body.innerHTML = printContent;//
      newWindow.print();   //
      newWindow.close();
      return false;
    },
    /**
     * 
     * @param  {[type]} val []
     * @return {[type]}     []
     */
    handleSizeChange(val) {
      this.pageSize = val
      this.pageNum = 1
      this.loadPricePage()
    },
    /**
     * 
     * @param  {[type]} val []
     * @return {[type]}     []
     */
    handleCurrentChange(val) {
      this.pageNum = val
      this.loadPricePage()
    },
    /**
     *
     * @return {[type]} []
     */
    loadPricePage() {
      var that = this
      let json = {
        beginDateOne: that.searchFrom.forceTime[0] === undefined ? '' : common.dateFmt("yyyy-MM-dd hh:mm:ss", that.searchFrom.forceTime[0]),
        beginDateTwo: that.searchFrom.forceTime[1] === undefined ? '' : common.dateFmt("yyyy-MM-dd hh:mm:ss", that.searchFrom.forceTime[1]),
        price: that.searchFrom.price,
        code: that.searchFrom.code,
        status: that.searchFrom.status ? 0 : '',
        pageNum: that.pageNum,
        pageSize: that.pageSize
      }
      //token
      let token = common.storage.get('token')
      pricePage(json, token).then(res => {
        let resJson = res.data
        if (resJson && resJson.code === 0) {
          //
          that.total = resJson.data.total
          that.pageNum = resJson.data.pageNum
          that.pageSize = resJson.data.pageSize
          that.tableData = resJson.data.list
        } else {
          //
          that.$message.warning('')
        }
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

<style lang="css">
.upload-file {
  display: inline-block;
}
.el-upload-list {
  display: none;
}
</style>



